.welcome {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  font-family: exo;

  .motto,
  .title {
    position: relative;
    width: 50%;
  }

  .motto {
    background: linear-gradient(-45deg, #79b9f5, #ece2cc);
    font-size: 1.2rem;
    padding: 1rem 1vw 1rem 1vw;
    text-align: center;
    border-top-left-radius: 7vw;
    border-bottom-left-radius: 7vw;
  }

  .title {
    background: linear-gradient(55deg, #5ca1fc, #ece2cc);
    font-size: 1.4rem;
    padding: 1rem 1vw 1rem 1vw;
    text-align: center;
    border-top-right-radius: 7vw;
    border-bottom-right-radius: 7vw;
  }
}

.checkout {
  position: relative;
  font-family: exo;
  text-align: center;
  font-size: 2rem;
  margin: 2.5rem 4vw 0 4vw;
  line-height: 3rem;
  display: inline-block;
  padding-top: 1.7rem;
  color: #422a03;
  text-shadow: -0.06rem -0.06rem #e4e9f0, 0.06rem -0.06rem #ebf1f8, -0.06rem 0.06rem #dfe5ec, 0.06rem 0.06rem #d6dde6;
}

.indicators {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  .button {
    cursor: pointer;
    position: relative;
    border-radius: 1rem;

    .background {
      margin: 0.2rem;
      clip-path: circle(49% at 50% 50%);
      width: 15vw;
    }

    .sub-title {
      font-family: exo;
      padding: 1rem 1vw 1rem 1vw;
      background: linear-gradient(45deg, #ffef72, #a5aef0);
      border-radius: 0.3rem;
      width: 15vw;
      font-size: 0.85rem;
      color: #422a03;
      filter: drop-shadow(0rem 0.2rem 0.25rem #78c99b);
      text-align: center;
    }
  }
}

.slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 7rem;
  transition: transform 0.5s ease-in-out;

  .text {
    font-family: exo;
    font-size: 1.2rem;
    color: #0d1e10;
    margin: 0rem 3vw 1rem 3vw;
    padding: 0 0 1.5rem 0;
  }

  .link {
    position: relative;
    bottom: 1rem;
    background: linear-gradient(45deg, #ffef72, #93b9ec);
    display: flex;
    flex-direction: row;
    align-items: center;
    text-decoration: none;
    transition: filter 0.2s ease-in-out, transform 0.3s ease-in-out;
    border-radius: 1rem;
    filter: drop-shadow(0rem 0.1rem 0.15rem #1e5c3c);

    &:hover {
      transform: scale(1.1);
      filter: drop-shadow(0rem 0.1rem 0.15rem #749688);
      background-color: #68c993;
    }

    .to {
      position: relative;
      margin: 1rem 1vw 1rem 2vw;
      color: #422a03;
      font-size: 1.2rem;
      overflow: hidden;
      font-family: exo;

      &:hover {
        text-decoration: underline;
      }
    }

    .right {
      margin-right: 1vw;
      color: #422a03;
      font-size: 1.3rem;
    }

    .image {
      margin-right: 2vw;
      width: 15vw;
      filter: drop-shadow(-0.2rem -0.2rem 0.2rem #38b1e0);
    }
  }
}
