.contact_form {
  background: linear-gradient(55deg, #f7b7a3, #9bc2f5);
  border-radius: 1rem;
  padding: 4% 3% 6% 3%;
  margin: 2rem 5vw -3rem 5vw;
  font-family: exo;

  .title {
    color: #1d1d1c;
    font-size: 1.7rem;
    text-align: center;
  }

  .form {
    background: #ffffff;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem 2vw;
    padding: 7% 3% 7% 3%;
    filter: drop-shadow(0rem 0.2rem 0.5rem #262527);

    .field {
      align-items: end;
      font-size: 1.1rem;
      color: #404244;
    }

    .input {
      font-family: exo;
      border: solid 0.15rem #f4f3f8;
      border-radius: 0.3rem;
      background: linear-gradient(55deg, #f7b7a3, #9bc2f5);
      box-shadow: 0rem 0.2rem 0.5rem rgba(0, 0, 0, 0.5);

      &.data {
        padding: 1% 4% 1% 4%;
      }

      &.message {
        width: 40%;
        height: 10rem;
        resize: none;
      }
    }

    .choice {
      color: #404244;
      font-family: exo;
      border: solid 0.15rem #f4f3f8;
      border-radius: 0.33rem;
      padding: 1% 5% 1% 5%;
      background: linear-gradient(55deg, #f7b7a3, #9bc2f5);
      box-shadow: 0rem 0.2rem 0.5rem rgba(0, 0, 0, 0.5);
    }

    .send {
      border: 0.2rem solid;
      cursor: pointer;
      font-family: exo;
      background: #e6e7e9;
      margin-top: 5%;
      color: hsl(0, 14%, 89%);
      padding: 3% 4% 3% 4%;
      text-shadow: -0.06rem -0.06rem #5f5c5c, 0.06rem -0.06rem #5f5c5c,
        -0.06rem 0.06rem #5f5c5c, 0.06rem 0.06rem #5f5c5c;

      &:hover {
        background: #fcfaf9;
        color: #f3fdf8;
        border: 0.2rem solid;
        border-image: linear-gradient(to right, #7a9ece, #fa7851);
        border-image-slice: 1;
        background-clip: padding-box;
        filter: drop-shadow(0rem 0.2rem 0.1rem #bbb4c2);
        text-shadow: -0.06rem -0.06rem #000000, 0.06rem -0.06rem #000000,
          -0.06rem 0.06rem #000000, 0.06rem 0.06rem #000000;
      }
    }
  }
}

.contact {
  background-color: #ffffff;
  padding: 1rem;
  border-radius: 0.4rem;
  margin: 3rem;

  &.webapp {
    filter: drop-shadow(0rem 0.1rem 0.3rem #bad0e7);
  }

  &.marketing {
    filter: drop-shadow(0rem 0.1rem 0.3rem #a9a9ac);
  }

  &.graphics {
    filter: drop-shadow(0rem 0.1rem 0.3rem #a5829f);
  }

  &.outsourcing {
    filter: drop-shadow(0rem 0.1rem 0.3rem #86ca7d);
  }

  &.support {
    filter: drop-shadow(0rem 0.1rem 0.3rem #fadb75);
  }

  .title {
    font-family: "exo";
    letter-spacing: 0.1rem;
    font-size: 2rem;
    text-align: center;
    color: #222221;
  }

  .text {
    margin-left: 0.5rem;
    padding-right: 30vw;
    font-size: 1rem;
    font-family: "exo";
    color: #0b151f;
  }

  .button {
    position: absolute;
    right: 25vw;
    margin-top: -8rem;
    border: none;
    cursor: pointer;
    line-height: 3.3rem;

    .icon,
    .letters {
      display: block;
      position: absolute;
      width: 21vw;
      letter-spacing: 0.1rem;
      height: 3.3rem;
      font-family: exo;
      font-size: 1.1rem;
      box-shadow: inset 0.02rem 0.02rem 0.02rem 0rem #ffffff80,
        0.04rem 0.04rem 0.04rem 0rem #0000001a,
        0.06rem 0.06rem 0.06rem 0rem #0000001a;
      text-align: center;
      box-sizing: border-box;
      transition: all 0.3s;

      &.webapp {
        background: linear-gradient(-45deg, #a6daf8, #81a2ff);
        color: #422a03;
        filter: drop-shadow(0.1rem 0rem 0.1rem rgb(162, 227, 243));
      }

      &.marketing {
        background: linear-gradient(-45deg, #d1d1fd, #aea9ce);
        color: #422a03;
        filter: drop-shadow(0.1rem 0rem 0.1rem rgb(186, 189, 183));
      }

      &.graphics {
        background: linear-gradient(-45deg, #e7acd8, #fd79f2);
        color: #422a03;
        filter: drop-shadow(0.1rem 0rem 0.1rem rgb(248, 139, 243));
      }

      &.outsourcing {
        background: linear-gradient(-45deg, #a0fc94, #b4fd79);
        color: #422a03;
        filter: drop-shadow(0.1rem 0rem 0.1rem rgb(182, 247, 118));
      }

      &.support {
        background: linear-gradient(-45deg, #f3f590, #f7ed65);
        color: #422a03;
        filter: drop-shadow(0.1rem 0rem 0.1rem rgb(244, 247, 100));
      }
    }

    .icon {
      font-size: 1.5rem;
      transform: rotateX(90deg);
      transform-origin: 50% 50% -1.53rem;
    }

    .letters {
      transform: rotateX(0deg);
      transform-origin: 50% 50% -1.53rem;
    }

    &:hover {
      .icon {
        box-shadow: inset 0.02rem 0.02rem 0.02rem 0rem #ffffff80,
          0.07rem 0.07rem 0.12rem 0rem #0000001a,
          0.04rem 0.04rem 0.05rem 0rem #0000001a;
        transform: rotateX(0deg);
      }

      .letters {
        box-shadow: inset 0.02rem 0.02rem 0.02rem 0rem #ffffff80,
          0.07rem 0.07rem 0.12rem 0rem #0000001a,
          0.04rem 0.04rem 0.05rem 0rem #0000001a;
        color: transparent;
        transform: rotateX(-90deg);
      }
    }
  }
}
