footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 1;

  .arrow {
    width: 20%;
    margin: 0;
    padding: 0;
    text-align: center;
    position: relative;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    font-size: 4rem;
    display: block;
    overflow: hidden;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.3);
  }

  .content {
    position: relative;
    background: linear-gradient(-45deg, #fca849, #f8e091);
    display: flex;
    flex-direction: row;
    font-family: exo;
    width: 100vw;
    height: 25rem;
    filter: drop-shadow(0rem 0.2rem 0.4rem #faa85b);

    .company {
      margin-left: 8vw;

      .title {
        margin-block-start: 2rem;
        margin-block-end: 0;
        color: #48484e;
      }
      .text {
        color: #0d1e10;
      }
    }

    .services {
      text-align: center;
      margin-top: 3rem;
      display: flex;
      margin-left: 9vw;
      flex-direction: column;

      .title {
        font-size: 1.25rem;
        color: #48484e;
      }

      .link {
        color: #ffffff;
        margin: 0.1rem;
        font-size: 1.05rem;
        letter-spacing: 0.1rem;
        text-decoration: none;
        text-shadow: -0.06rem -0.06rem #080808, 0.06rem -0.06rem #080808,
          -0.06rem 0.06rem #080808, 0.06rem 0.06rem #080808;
        transform: scale(1.08);
        filter: drop-shadow(0rem 0.15rem 0.2rem #afaeab);

        &:hover {
          transform: scale(1.1);
        }
      }
    }

    .contact_info {
      margin-top: 6rem;
      margin-right: 5vw;
      text-align: center;

      .title {
        color: #48484e;
      }
      .text {
        color: #0d1e10;
      }
    }

    .copyright {
      position: absolute;
      bottom: 0;
      width: 100%;
      text-align: center;

      .image {
        width: 3vw;
        margin: 0.2rem 0.4vw -0.2rem 0.4vw;
      }
    }
  }

  .envelope {
    top: 8rem;
    right: 2vw;
    filter: drop-shadow(0.1rem 0.2rem 0.1rem rgb(101, 97, 19));
    position: absolute;
    height: 4rem;
    width: 8rem;
    transition: transform 0.4s ease-in-out;
    z-index: 3;

    .paper {
      position: absolute;
      background-color: #fffcf1;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      height: 4rem;
      width: 8rem;
      animation: closePaper 0.2s 0.2s both;

      @keyframes closePaper {
        0% {
          z-index: 3;
          transform: translateY(-2rem);
        }
        100% {
          transform: translateY(0);
        }
      }

      .text {
        font-family: "Cantora One";
        text-decoration: none;
        font-size: 0.8rem;
        position: relative;
        margin: 0rem;
        padding: 0.1rem;
        color: rgb(52 52 60);
      }

      .logo {
        position: relative;
        height: 1rem;
        width: 1rem;
        top: 0.3rem;
        left: 0rem;
      }
    }

    .triangle {
      position: absolute;
      width: 0;
      height: 0;

      &.left,
      &.right,
      &.bottom {
        z-index: 4;
      }

      &.top,
      &.bottom {
        border-left: 4rem solid transparent;
        border-right: 4rem solid transparent;
      }

      &.top {
        filter: drop-shadow(0rem 0.1rem 0.05rem white);
        border-top: 2.5rem solid #0d1e10;
        animation: closeTop 0.2s ease-in-out 0.4s both;

        @keyframes closeTop {
          0% {
            transform-origin: top;
            transform: rotateX(180deg);
          }
          100% {
            transform-origin: top;
            transform: rotateX(0deg);
            z-index: 5;
          }
        }
      }

      &.bottom {
        filter: drop-shadow(0rem -0.1rem 0.05rem white);
        border-bottom: 2.25rem solid #0d1e10;
        bottom: 0rem;
      }

      &.left,
      &.right {
        border-top: 2rem solid transparent;
        border-bottom: 2rem solid transparent;
      }

      &.left {
        border-left: 4rem solid #0d1e10;
      }

      &.right {
        right: 0rem;
        border-right: 4rem solid #0d1e10;
      }
    }

    &:hover {
      transform: translateY(-0.5rem);

      .paper {
        animation: openPaper 0.2s ease-in-out both;

        @keyframes openPaper {
          0% {
            transform: translateY(0);
          }
          100% {
            transform: translateY(-2rem);
            z-index: 4;
          }
        }
      }

      .top {
        animation: openTop 0.2s ease-in-out both;

        @keyframes openTop {
          0% {
            transform-origin: top;
            transform: rotateX(0deg);
          }
          100% {
            transform-origin: top;
            transform: rotateX(180deg);
            z-index: 3;
          }
        }
      }

      .left,
      .right,
      .bottom {
        z-index: 5;
      }
    }
  }

  .drawer {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0;
    position: fixed;
    left: -10.5vw;
    bottom: 2rem;
    background: #c6c6c6;
    color: rgb(0, 0, 0);
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
    transition: left 0.3s ease;
    overflow: hidden;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.3);
    background: linear-gradient(#96a69b, #83d784);
    background-size: 300% 100%;

    &:hover {
      left: 0;

      .open_drawer {
        opacity: 0;
      }
    }

    .buttons {
      display: flex;
      flex-direction: column;

      .button {
        color: #414642;
        border: none;
        border-top-right-radius: 50%;
        border-bottom-right-radius: 50%;
        margin-top: 0.25rem;
        margin-bottom: 0.25rem;
        padding: 0.5rem;
        font-family: exo;
        filter: drop-shadow(0.1rem 0.1rem 0.1rem rgba(167, 158, 158, 0.456));

        &.cookies {
          background: radial-gradient(circle, #ffffff, #e8ca36);
        }

        &.rules {
          background: radial-gradient(circle, #ffffff, #e89e36);
        }

        &.privacy {
          background: radial-gradient(circle, #ffffff, #e84236);
        }

        &:hover {
          scale: 1.1;
          box-shadow: 0 0 0.3rem #343434;
          transition: 0.35s ease-in-out;
        }
      }
    }

    .open_drawer {
      font-size: 1.2rem;
      background: none;
      border: none;
      color: black;
      transform: scale(-1);
      transform: rotate(-90deg);
    }
  }
}
