header {
  background: linear-gradient(90deg, #acadad, #a2a2a3);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  width: 100%;
  height: 7.55rem;

  .company {
    background: #feffff;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20vw;
    padding: 0.1rem;
    font-family: "Anek Gurmukhi";
    filter: drop-shadow(0.1rem 0.15rem 0.15rem #e8ebe5);

    .logo {
      position: relative;
      height: 3.4rem;
      width: 3.3rem;
    }

    .name {
      position: relative;
      margin: 0.1rem 2vw 0 2vw;
      padding: 0;
      font-size: 2.6rem;

      .I,
      .C {
        font-size: 2.1rem;
        padding: 0 0.6vw 0 1vw;
      }
    }

    .code {
      margin: 0;
      padding: 0;
      font-size: 0.777rem;
      line-height: 0.999rem;
      font-family: "Anek Gurmukhi";
    }
  }

  .activator {
    display: none;

    @media only screen and (orientation: portrait) {
      cursor: pointer;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: 2.5rem;
      right: 6vw;
      overflow: hidden;
      background: transparent;
      border: none;
      filter: drop-shadow(0 0 0.1rem white);
    }

    .line {
      background: #0d1e10;
      position: relative;
      height: 0.1rem;
      margin: 0.22rem;
      width: 1.69rem;
      border: solid 0.1rem #0d1e10;
      transform-origin: left center;
    }
  }

  .navigation {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    position: relative;
    align-items: center;
    top: 1.5rem;

    @media only screen and (orientation: portrait) {
      flex-direction: column;
      align-items: flex-end;
      position: absolute;
      top: 7.1rem;
      right: 0;
    }

    .link {
      text-decoration: none;

      .button {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-family: goldman;
        background: none;
        border: solid 0.11rem #feffff;
        border-radius: 0.2rem;
        padding: 0.2rem;
        margin: 0.24rem;
        filter: drop-shadow(0rem 0rem 0.1rem #eff3f3);

        @media only screen and (orientation: portrait) {
          border: none;
        }

        .icon,
        .letters {
          padding: 0.2rem;
          border-radius: 0.2rem;

          @media only screen and (orientation: portrait) {
            padding: 0.58rem;
            text-align: start;
            filter: drop-shadow(0rem 0rem 0.1rem #b5bbbb);
          }
        }

        .icon {
          font-size: 1.2rem;
          color: #0d1e10;
          background: linear-gradient(-45deg, #5dbe50, #80e473);

          @media only screen and (orientation: portrait) {
            width: 75vw;
          }
        }

        .letters {
          background: linear-gradient(-45deg, #fca849, #f8e091);
          transition: transform 0.2s, scale 0.1s;

          @media only screen and (orientation: portrait) {
            width: 70vw;
          }

          &:hover {
            filter: drop-shadow(0 0.15rem 0.1rem #8d836f);
            transform: translateY(-10%);
            scale: 1.015;
          }
        }
      }
    }
  }
}
