main {
  margin: 3rem 2vw 0 2vw;

  .info {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: exo;

    .title {
    }
    .image {
      height: 11rem;
      filter: drop-shadow(0rem 0.2rem 0.1rem #86837e);
    }

    .container {
      .title {
      }
      .sub-text {
      }
      .quote {
      }
    }
  }

  .goals {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: exo;
    .title {
    }
    .image {
    }
    .container {
      .sub-title {
      }
      .sub-text {
      }
    }
  }

  .opinion {
    .title {
      font-size: 2rem;
      background: linear-gradient(0deg, #9999f8, #a0a0fd);
      margin-top: 3rem;
      border-radius: 0.4rem;
      padding: 1rem;
      font-family: goldman;
      text-align: start;
    }

    .window {
      display: flex;
      flex-direction: row;
      align-items: center;
      background: linear-gradient(-30deg, #3972dd, #d9dddd);
      position: absolute;
      border-radius: 1rem;
      margin: 2rem 6vw 0rem 0vw;
      width: 100%;
      height: 25rem;
      opacity: 0;
      transition: opacity 0.3s ease-out;
      border-right: 2rem solid #fffefc;
      border-top-right-radius: 50%;

      &.active {
        opacity: 1;
      }

      .text {
        width: 100%;
        display: flex;
        font-size: 1rem;
        padding: 0 1vw 0rem 1vw;
        flex-direction: column;
        align-items: center;
        text-align: center;

        .name {
          border-bottom: solid 0.31rem;
          padding-bottom: 1rem;
          border-bottom-right-radius: 0.8rem;
          display: flex;
          justify-content: space-between;
          font-family: goldman;
          position: absolute;
          top: 2rem;
          color: white;
        }

        .five {
          margin-top: 2%;

          .star {
            color: #e4c724;
            font-size: 1.3rem;
          }
        }

        .comment {
          font-family: exo;
          font-size: 1rem;
          margin-bottom: -4rem;
        }
      }
    }
  }
}
