.fucc {
  margin-top: 15rem;
}

.diagram {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-top: -8rem;

  .title {
    line-height: 2.2rem;
    position: absolute;
    left: 10vw;
    font-size: 2.5rem;
    text-align: center;
  }

  .parts {
    color: black;
    position: absolute;
    filter: drop-shadow(0rem 0rem 0.033rem black);
    cursor: default;

    &.utopia {
      font-size: 1.5rem;
      margin: 7% 0% 0% 24%;
    }

    &.expensive {
      transform: rotate(30deg);
      font-size: 1.1rem;
      margin: 0% 0% 3% 5%;
    }

    &.more_time {
      transform: rotate(-30deg);
      font-size: 1.1rem;
      margin: 0% 0% 3% 44%;
    }

    &.awry {
      font-size: 1.1rem;
      margin: 33% 0% 0% 24%;
      white-space: pre;
      line-height: 99%;
      text-align: center;
    }

    &.for_free {
      font-size: 1.2rem;
      margin: 0% 31% 24% 0%;
      transform: rotate(25deg);
    }

    &.qualitatively {
      font-size: 1.8rem;
      margin: 0% 0% 31% 30%;
    }

    &.fast {
      font-size: 1.8rem;
      margin: 25% 15% 0% 0%;
    }

    &.cheaply {
      font-size: 1.8rem;
      margin: 25% 0% 0% 60%;
    }

    &.really {
      transform: rotate(-10deg);
      font-size: 1rem;
      margin: 0% 10.5% 29% 0%;
      white-space: pre;
    }

    &.massacre {
      font-size: 1rem;
      margin: 0% 10% 12.8% 0%;
      transform: rotate(25deg);
    }

    &.trash {
      transform: rotate(-20deg);
      font-size: 1rem;
      margin: 0% 23.5% 5% 0%;
    }

    &:hover {
      scale: 1.05;
      filter: drop-shadow(0rem 0rem 0.1rem white);
    }
  }

  .background {
    width: 100%;
  }
}

.table {
  margin: 3%;
  border: solid 0.15rem black;
  font-family: exo;
  filter: drop-shadow(0rem 0.2rem 0.2rem #151811);
  border-radius: 0.6rem;

  .head {
    .block {
      font-size: 1.2rem;
      text-decoration: underline;
      background-color: rgb(239, 238, 245);
      border-radius: 0.5rem;
    }
  }

  .body {
    .category {
      background-color: #ebf58f;
      font-size: 1.3rem;
      padding: 1rem;
      text-align: end;
      border-radius: 1rem;
      border-bottom: solid 0.15rem #1b1919;
    }

    .price {
      background-color: #cee9e7;
      padding: 1rem;
      font-size: 1rem;
      text-align: center;
      border-radius: 1rem;
      border-bottom: solid 0.15rem #1b1919;
    }

    .description {
      background-color: #b5f8b5;
      font-size: 1rem;
      padding: 1rem;
      text-align: start;
      border-radius: 1rem;
      border-bottom: solid 0.15rem #1b1919;
    }
  }
}
