main {
  margin: 8rem 2vw 0rem 2vw;

  .aida {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: exo;
    font-size: 1rem;
    border-radius: 0.4rem;
    filter: drop-shadow(0rem 0.2rem 0.2rem #676568);

    .title {
      position: relative;
      font-family: goldman;
      font-size: 2rem;
      color: aliceblue;
      text-shadow: -0.06rem -0.06rem #080808, 0.06rem -0.06rem #080808,
        -0.06rem 0.06rem #080808, 0.06rem 0.06rem #080808;
    }

    .background {
      position: relative;
      filter: drop-shadow(0rem 0.2rem 0.3rem #676568);
      background: linear-gradient(90deg, #ff7e5f, #feb47b);
      clip-path: polygon(0 0, 100% 0, 100% 65%, 50% 100%, 0 65%);
      padding-bottom: 2.5rem;
      border-radius: 0.5rem;

      .image {
        height: 11rem;
        position: relative;
      }
    }

    .container {
      position: relative;
      background: linear-gradient(90deg, #ff7e5f, #feb47b);
      padding: 1rem;
      border-radius: 0.4rem;
      display: flex;
      align-items: center;

      .image {
        height: 8rem;
        filter: drop-shadow(0rem 0.2rem 0.1rem #f1f5f7);
      }

      &.desire {
        flex-direction: row;
      }

      &.interest,
      &.action {
        text-align: end;
        flex-direction: row-reverse;
      }
    }
  }

  .article {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: exo;
    font-size: 1rem;
    border-radius: 0.4rem;

    .title {
      position: relative;
      font-family: exo;
      font-size: 2rem;
      color: #000000;
    }

    .image {
    }

    .text {
    }

    .container {
      .sub-title {
        text-align: center;
        border-radius: 0.3rem;
        background: linear-gradient(0deg, #5fffdc, #adb7e6);
        padding: 0.2rem;
        font-size: 1.3rem;
      }
      .sub-text {
      }
    }

    .ending {
    }
  }

  .list {
    margin: 0 2vw 0 2vw;
    border-image-slice: 1;
    background-clip: padding-box;
    border-radius: 0.4rem;
    background: #facd3b;
    filter: drop-shadow(0rem 0.2rem 0.1rem #86837e);
    padding: 2rem 2vw 1rem 2vw;

    .title {
      font-family: "goldman";
      font-size: 2rem;
      text-align: center;
      margin: 2vw 2vw 2rem 0;
    }

    .sub-title {
      text-align: center;
      font-family: "exo";
      text-transform: uppercase;
      font-size: 1.1rem;
      color: #161616;
    }

    .topic-title,
    .benefits-title {
      text-transform: uppercase;
      text-align: start;
      font-family: "goldman";
      font-size: 1rem;
      color: #161616;
      padding: 0.2rem;
      border-radius: 0.3rem;
      background: white;
    }

    .text {
      list-style-type: square;
      font-size: 1rem;
      font-family: "exo";
      text-align: start;
    }

    .ending {
      padding: 1rem;
      font-family: "exo";
      text-transform: uppercase;
      font-size: 1.2rem;
      text-align: center;
      margin: 2rem 0vw 2rem 0;
      border-radius: 0.3rem;
      filter: drop-shadow(0rem 0.2rem 0.1rem #86837e);
      color: #161616;
      background-color: white;
    }
  }

  .couple {
    margin: 2rem 2vw 0 2vw;

    .title {
      font-size: 2rem;
      text-align: center;
    }

    .container {
      padding: 0.3rem;

      .sub-title {
        font-size: 1.6rem;
        background-color: #3d4640;
        border-radius: 0.2rem;
        text-align: center;
        padding: 0.2rem;
        color: #f5f3ef;
      }

      .man,
      .woman {
        padding: 0.2rem;
        margin: 0.3rem;
        border-radius: 0.3rem;
        font-size: 1rem;
        filter: drop-shadow(0rem 0.2rem 0.1rem #86837e);
      }

      .first,
      .second {
        font-family: exo;
      }

      .first {
        .man {
          background: linear-gradient(-45deg, #f3dc58, #ecff3e);
        }
      }

      .second {
        .woman {
          background: linear-gradient(-45deg, #5dbe50, #80e473);
        }
      }
    }
    .ending {
      font-family: exo;
    }
  }
}
